@keyframes move-1 {
  to {
    left: calc(100% + 100px);
    bottom: 30%;
  }
}

@keyframes move-2 {
  to {
    left: calc(100% + 100px);
    bottom: 70%;
  }
}

@keyframes move-3 {
  to {
    left: 50%;
    bottom: 100%;
  }
}

@keyframes move-4 {
  39% {
    left: calc(100% + 100px);
    bottom: 25%;
  }
  40% {
    left: -100px;
    bottom: 0%;
  }
  100% {
    left: 80%;
    bottom: 20%;
  }
}

@keyframes move-5 {
  39% {
    left: calc(100% + 100px);
    bottom: 65%;
  }
  40% {
    left: -100px;
    bottom: 35%;
  }
  100% {
    left: 80%;
    bottom: 50%;
  }
}

@keyframes move-6 {
  39% {
    left: calc(100% + 100px);
    bottom: 100%;
  }
  40% {
    left: -100px;
    bottom: 70%;
  }
  100% {
    left: 85%;
    bottom: 90%;
  }
}
